import styled from "styled-components";

export const Container = styled.section `
    margin: auto;
    border-bottom: solid 8px #222;
`;

export const Frame = styled.div `
    max-width: 1200px;
`;
export const Inner = styled.div `
    color:#fff;
    display: flex;
    font-size: 26px;
    flex-direction: column;
    padding: 70px 45px;
    max-width: 815px;
    margin: auto;
`;
export const Item = styled.div `
    margin-bottom: 10px;
    &:first-of-type{
        margin-top :3em
    }
`;
export const Title = styled.h1 `
    font-size : 50px;
    line-height : 1.1;
    margin-bottom: 8px;
    margin-top: 0;
    text-align: center;

    @media (max-width: 600px){
        font-size: 35px;
    }
`;

export const Header = styled.div `
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    width: 100%
    cursor: pointer;
    margin-bottom: 1px;
    padding: 0.8em 1.2em;
    align-items: center;
    background: #303030;
    font-weight: normal;
    user-select: none;

    img{
        filter: brightness(0) invert(1);
        width: 24px;
        @media (max-width :600px){
            width: 16px;
        }
    }

    @media (max-width: 600px) {
        font-size: 16px;
    }
`;

export const Body = styled.div `
    font-weight: normal;
    background: #303030;
    box-sizing: border-box;
    max-width: 1200px;
    padding: 0.8em 1.2em;
    user-select: none;
    align-items: center;
    line-height: normal;
    transition: max-height 0.25s cubic-bezier(0.5, 0, 0.1, 1);

    @media (max-width: 600px) {
        font-size: 16px;
        line-height: 22px;
    }
`;