import {React, useState, createContext, useContext} from "react";
import { Container, Item, Inner, Title,Body, Header, Frame } from "./style/accordin";

const ToggleContext = createContext();

export default function Accordin ({children,...restProps}){
    return (
        <Container {...restProps}>
            <Inner>{children}</Inner>
        </Container>
        );
}

Accordin.Frame = function AccordinFrame ({children,...restProps}){
    return <Frame {...restProps}>{children}</Frame>;
}

Accordin.Item = function AccordinItem ({children,...restProps}){
    const [toggleshow, setToggleShow] = useState(false);
    return (
        <ToggleContext.Provider value = {{toggleshow, setToggleShow}}>
            <Item {...restProps}>{children}</Item>
        </ToggleContext.Provider>
        );
}

Accordin.Title = function AccordinItem ({children,...restProps}){
     return <Title {...restProps}>{children}</Title>;
}

Accordin.Header = function AccordinItem ({children,...restProps}){
    const {toggleshow,setToggleShow} = useContext(ToggleContext);
    return <Header onClick = {()=>setToggleShow(!toggleshow)} {...restProps}>
        {children}
        {toggleshow ? (
            <img src= "images/icons/close-slim.png" alt='close'/>
        ) :
        (
            <img src="images/icons/add.png" alt='add'/>
        )}
        </Header>;
}

Accordin.Body = function AccordinItem ({children,...restProps}){
    const{toggleshow} = useContext(ToggleContext);
    return toggleshow ? <Body {...restProps}>{children}</Body> : null;
}