import React from 'react';
import { Accordin } from '../components';
import faqs from '../fixtures/faqs.json';
import { Optform } from '../components';

export function FaqsContainer(){
    return (
        <Accordin>
            <Accordin.Title>Frequently Asked Questions</Accordin.Title>
            <Accordin.Frame>
                {faqs.map((item) => (
                    <Accordin.Item key = {item.id}>
                        <Accordin.Header>{item.header}</Accordin.Header>
                        <Accordin.Body>{item.body}</Accordin.Body>
                    </Accordin.Item>
                ))}
            </Accordin.Frame>
            <Optform>
                <Optform.Input placeholder="Email Address"/>
                <Optform.Button>Get Started</Optform.Button>
                <Optform.Text>Ready to watch? Enter your email to create or restart
                your membership.</Optform.Text>
            </Optform>
        </Accordin>
    );
}