import React,{useState, useContext} from 'react';
import { useHistory } from 'react-router-dom';
import { FirebaseContext } from '../contex/firebase';
import HeaderContainer from '../containers/header';
import FooterContaier from '../containers/footer';
import { Form } from '../components';
import * as ROUTES from '../constants/route'

export default function Signup(){
    const history = useHistory();
    const { firebase } = useContext(FirebaseContext);
    const [firstName, setFirstName] = useState('');
    const [email, setEmail] = useState('');
    const [passWord, setPassWord] = useState('');
    const [error, setError] = useState('');

    const inValid = firstName ==='' | email === '' | passWord === '';

    const handleSignUp = (event) => {
        event.preventDefault();
        firebase
            .auth()
            .createUserWithEmailAndPassword(email, passWord)
            .then((result) => 
                    result.user.
                    updateProfile({
                        displayName : firstName,
                        photoURL : Math.floor(Math.random()*5)+1
                    }))
                .then(()=> {
                    setEmail('');
                    setPassWord('');
                    setFirstName('');
                    history.push(ROUTES.BROWSE)
                })
            .catch((error) => setError(error.message()))
    };

    return (
        <>
            <HeaderContainer>
                <Form>
                    <Form.Title>Signup</Form.Title>
                    {error && <Form.Error>{error}</Form.Error>}
                    <Form.Base onClick = {handleSignUp} method = 'Post'>
                        <Form.Input
                            placeholder = 'First Name'
                            value = {firstName}
                            onChange = {({target})=>setFirstName(target.value)}
                        
                        />
                        <Form.Input
                            placeholder = 'Email'
                            value = {email}
                            onChange = {({target})=>setEmail(target.value)}
                        />
                        <Form.Input
                            type='password'
                            placeholder = 'Password'
                            value={passWord}
                            onChange = {({target})=>setPassWord(target.value)}
                            autoComplete = 'off'
                        />
                        <Form.Submit disabled = {inValid} type='submit'>
                            Signup
                        </Form.Submit>
                        <Form.Text>Already a user? 
                            <Form.Link to ={ROUTES.SIGN_IN} >
                                Sign Up Now.
                            </Form.Link>
                        </Form.Text>
                        <Form.TextSmall>
                            This page is protected by Google reCAPTCHA.
                        </Form.TextSmall>              
                    </Form.Base>
                </Form>
            </HeaderContainer>
            <FooterContaier/>
        </>
    )
}