import styled from 'styled-components';

export const Container = styled.div `
    display: flex;
    justify-content: center;
    height: 100%
    margin-top: 20px;
    flex-wrap: wrap;

    @media (max-width: 1000px){
        flex-direction: column;
        align-items: center;

    }
`;
export const Input = styled.input `
    max-width: 450px;
    width: 100%;
    border: 0;
    padding: 10px;
    height: 70px;
    box-sizing: border-box;
`;
export const Button = styled.button `
    display: flex;
    align-items: center; 
    height: 70px;
    background-color: #e50914;
    color: white;
    text-transform: uppercase;
    padding: 0 32px;
    font-size: 26px;
    border: 0;
    cursor: pointer;
    margin-top: 0;
       

    img{
        width: 24px;
        margin-left: 10px;
        filter: brightness(0) invert(1);

        @media(max-width: 1000px){
            width: 16px;
        }
    }

    &:hover {
        background: #f40612;
    }

    @media(max-width: 600px){
        height: 50px;
        font-size: 16px;
        margin-top: 20px;
        font-weight: bold;
    }
`;
export const Text = styled.p `
    font-size: 19px;
    color: white;
    text-align: center;
    // order: -1;

    @media(max-width: 600px){
        font-size: 16px;
        line-height: 22px;
    }

`;