import Styled from 'styled-components'
import { Link as ReachRouterLink } from 'react-router-dom';

export const Container = Styled.section `
    display: flex;
    flex-direction: column;
    margin: auto;
    padding: 60px 68px 40px;
    width: 100%;
    max-width: 450px;
    min-height: 660px;
    background-color: rgba(0, 0, 0, 0.75);
    border-radius: 5px;
    padding: 60px 68px 40px;
    margin-bottom: 100px;

`;

export const Error = Styled.div ``;

export const Base = Styled.form `
    display: flex;
    flex-direction: column;
`;

export const Title = Styled.h1 `
    color: #fff;
    font-size: 32px;
    font-weight: bold;
    margin-bottom: 28px;
`;

export const Text = Styled.h1 `
    color: #737373;
    font-size: 16px;
    font-weight: 500;
`;

export const TextSmall = Styled.h2 `
    margin-top: 10px;
    font-size: 13px;
    line-height: normal;
    color: #8c8c8c;
`;

export const Link = Styled(ReachRouterLink)`
    text-decoration: none;
    cursor: pointer;
    color: #fff;
    font-size: 18px;
`;

export const Input = Styled.input `
    background: #333;
    border-radius: 4px;
    border: 0;
    color: #fff;
    height: 50px;
    line-height: 50px;
    padding: 5px 20px;
    margin-bottom: 20px;

    &:last-of-type {
        margin-bottom: 30px;
    }
`;

export const Submit = Styled.button `
    background: #e50914;
    border-radius: 4px;
    font-size: 16px;
    font-weight: bold;
    margin: 0 0 12px;
    padding: 16px;
    border: 0;
    color: white;
    cursor: pointer;

    &:disabled {
        opacity: 0.5;
    }
`;